<template>
<!--  -->
        <div class="register">
            <v-back :msg='msg'></v-back>
            <div class="register-box">
                <!-- <form action="#" class="form"> -->
                    <div class="username">
                        <input v-model="phone" type="text" id="username" placeholder="/">
                    </div>
                    <div class="password">
                        <input v-model="pw" type="password" id="password" placeholder="">
                    </div>
                    <input @click.stop="register" class="btn" type="submit" value=""> 
                <!-- </form> -->
            </div>
        </div>  
</template>
<script>
import back from '@/components/back/back.vue';
export default {
    data(){
        return {
            msg: '',
            phone: '15938785986',
            pw: '1234556'
        }
    },
    methods: {
        register(){
            this.$http.get(this.getIp()+'/mobile.php/member/add?phone='+this.phone+'&password='+this.pw)
                .then((res) => {
                    console.log(res);
                    this.$router.go(-2);
                    console.log("");
                    //    this.my = res.body.data;
                    // console.log(this.my);
            })
        }
    },
    created()
    {
        
    },
    components: {
        'v-back': back
    }
}
</script>
<style scoped lang="less" rel="stylesheet">
// 
.register {
        // position: absolute;
        // width: 100%;
        // height: 100%;
        // left: 0;
        // top: 50px;
        // z-index: 29;
        // background-color: #efeff4;
        padding-top: 50px;
        .register-box {
            padding: 20px;
            // .form {
                input#username,input#password {
                    border-radius: 20px;
                    border: none;
                     padding-left: 40px;
                    background: #fff url(../../../static/images/login_1.png) no-repeat 5px 5px;
                    background-size: 30px 30px;
                } 
                input#password {
                    background-image: url(../../../static/images/login_2.jpg);
                }
                .btn {
                    width: 100%;
                    border-radius: 20px;
                    background-color: #9e0028;
                    border: none;
                    font-size: 20px;
                }
            // }
        }
}
</style>



